<template>
  <div
    class="duo-input"
    :class="{
      'is-disabled': disabled
    }">
    <template v-if="type != 'textarea'">
      <input
        class="duo-input__inner"
        :name="name"
        :type="type"
        :maxlength="maxlength"
        :minlength="minlength"
        :disabled="disabled"
        :placeholder="placeholder"
        v-model="childValue">
    </template>
    <template v-else>
      <textarea
        class="duo-textarea__inner"
        :name="name"
        :disabled="disabled"
        :placeholder="placeholder"
        v-model="childValue">
      </textarea>
    </template>
  </div>
</template>

<script>
  export default {
    name: 'DuoInput',
    componentName: 'DuoInput',
    props: {
      type: {
        type: String,
        default: 'text'
      },
      value: [String, Number],
      maxlength: [Number],
      minlength: [Number],
      placeholder: String,
      clearable: Boolean,
      disabled: Boolean,
      name: String,

    },
    data() {
      return {
        childValue: ''
      }
    },
    methods: {
      change(value) {
        this.$emit('input', value)
      }
    },
    watch: {
      'childValue'() {
        this.change(this.childValue)
      },
      'value'() {
        if (this.value !== this.childValue) {
          this.childValue = this.value
        }
      }
    }
  }  
</script>

<style scoped>
.duo-input {
  display: inline-block;
  position: relative;
}
.duo-input__inner,
.duo-textarea__inner {
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  outline: none;
  transition: border .5s;
}
.duo-input__inner {
  padding: 0 15px;
  height: 40px;
}
.duo-textarea__inner {
  padding: 5px 15px;
  box-sizing: border-box
 }
.duo-input.is-disabled .duo-input__inner,
.duo-input.is-disabled .duo-textarea__inner {
  border-color: #e4e7ed;
  background-color: #f5f7fa;
  color: #c0c4cc;
  cursor: not-allowed;
}
.duo-input__inner:focus,
.duo-textarea__inner:focus {
  border: 1px solid #409eff;
  transition: border .5s;
}
input.duo-input__inner::-webkit-outer-spin-button,
input.duo-input__inner::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input.duo-input__inner.[type="number"] {
   -moz-appearance: textfield;
}
</style>


